*,
*::before,
*::after {
  box-sizing: border-box;
}

body {
  font-family: var(--txt-font);
  font-style: normal;
  margin: 0 auto;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  padding: 0;
}

ul,
ol {
  margin: 0;
  padding: 0;
}

p {
  margin: 0;
  padding: 0;
}

img {
  display: block;
}

ul {
  list-style: none;
}

li {
  text-decoration: none;
}
